






































































































































































import { Vue, Component } from 'vue-property-decorator'
import { CARD_STATUS } from '@/assets/constant'

interface Card {
    id?: string
    name: string
    holder: 'conan' | 'chaeji'
    deposit_bank: string
    annual_fee: string
    monthly_usage: string
    period_start: string
    period_end: string
    status: string
    debits?: Array<{
        name: string
        cost: string
    }>
}

interface MonthlyPayment {
    name: string
    cost: string
}

@Component
export default class CardDetail extends Vue {
    get card(): Card | null {
        return this.$store.getters.selectedCard || null
    }

    get isFormValid(): boolean {
        if (!this.card) return false
        
        return !!(
            this.card.name?.trim() &&
            this.card.holder &&
            this.card.period_start &&
            this.card.period_end
        )
    }

    getStatusText(status: string): string {
        const statusMap: Record<string, string> = {
            [CARD_STATUS.USING]: 'ÏÇ¨Ïö©Ï§ë',
            [CARD_STATUS.KEEPING]: 'Î≥¥Í¥ÄÏ§ë',
            [CARD_STATUS.TERMINATED]: 'Ìï¥ÏßÄÎê®'
        }
        
        return statusMap[status] || status
    }

    addMonthlyPayment(): void {
        if (!this.card) return
        
        this.$store.commit('addMonthlyPayment')
        this.$forceUpdate()
    }

    deleteMonthlyPayment(monthlyPayment: MonthlyPayment): void {
        if (!this.card) return
        
        this.$store.commit('deleteMonthlyPayment', monthlyPayment)
    }

    async saveCard(): Promise<void> {
        if (!this.card || !this.isFormValid) return
        
        const cardName = this.card.name || 'ÏÉà Ïπ¥Îìú'
        const confirmed = confirm(`${cardName}ÏùÑ(Î•º) Ï†ÄÏû•ÌïòÏãúÍ≤†ÏäµÎãàÍπå?`)
        
        if (confirmed) {
            try {
                await this.$store.dispatch('save')
                this.$emit('card-saved', this.card)
            } catch (error) {
                console.error('Failed to save card:', error)
                alert('Ïπ¥Îìú Ï†ÄÏû•Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
            }
        }
    }

    async deleteCard(): Promise<void> {
        if (!this.card || !this.card.id) return
        
        const cardName = this.card.name || 'Ïπ¥Îìú'
        const confirmed = confirm(`${cardName}ÏùÑ(Î•º) ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå? Ïù¥ ÏûëÏóÖÏùÄ ÎêòÎèåÎ¶¥ Ïàò ÏóÜÏäµÎãàÎã§.`)
        
        if (confirmed) {
            try {
                await this.$store.commit('delete')
                this.$emit('card-deleted', this.card)
            } catch (error) {
                console.error('Failed to delete card:', error)
                alert('Ïπ¥Îìú ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
            }
        }
    }

    formatCurrency(value: string): string {
        const num = parseFloat(value)
        if (isNaN(num)) return '0'
        return num.toLocaleString('ko-KR')
    }
}
